<template>
    <div class="container-fluid">
        <div class="row">

            <div class="col-12 d-flex justify-content-between content-time ">
                <h1>Historial</h1>
                <div class="d-flex flex-row flex-wrap">
                    <div class="text-left">
                        <p class="txt-time d-inline">Año</p>
                        <v-select class="" :items="years" label="2021" dense solo style="max-width:6rem"></v-select>
                    </div>
                    <div class="text-left d-inline ml-3">
                        <p class="txt-time d-inline ">Mes</p>
                        <v-select class="" :items="months" label="Octubre" dense solo style="max-width:9rem"></v-select>
                    </div>
                    <div class="text-left d-inline ml-3 centered-input">
                        <p class="txt-time d-inline">De</p>
                        <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false"
                            :return-value.sync="fromDate" transition="scale-transition" offset-y min-width="auto">
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field class="edt-date" v-model="fromDate" placeholder="---------------"
                                    append-icon="mdi-calendar" readonly v-bind="attrs" v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="fromDate" no-title scrollable color="#E4442E">
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu1 = false">
                                    Cancel
                                </v-btn>
                                <v-btn text color="primary" @click="$refs.menu1.save(fromDate)">
                                    OK
                                </v-btn>
                            </v-date-picker>
                        </v-menu>
                    </div>
                    <div class="text-left d-inline ml-3 centered-input">
                        <p class="txt-time d-inline">Hasta</p>
                        <v-menu ref="menu2" v-model="menu2" :close-on-content-click="false" :return-value.sync="toDate"
                            transition="scale-transition" offset-y min-width="auto">
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field class="edt-date" v-model="toDate" placeholder="---------------"
                                    append-icon="mdi-calendar" readonly v-bind="attrs" v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="toDate" no-title scrollable color="#E4442E">
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu2 = false">
                                    Cancel
                                </v-btn>
                                <v-btn text color="primary" @click="$refs.menu2.save(toDate)">
                                    OK
                                </v-btn>
                            </v-date-picker>
                        </v-menu>
                    </div>

                </div>
            </div>

            <div class="col-12">
                <table class="table">
                    <tr class="t-none">
                        <td class="t-head" v-for="(item, index) in heads" :key="index">{{item}}</td>
                    </tr>
                    <tr v-for="(o, key) in orders" :key="key">
                        <td>
                            <div class="t-user">
                                <img src="@/assets/Images/Vector.png" alt="" />
                                <div>
                                    <h3>{{ o.numOrder }}</h3>
                                    <p>{{ o.nameUser }}</p>
                                </div>
                            </div>
                        </td>
                        <td class="t-restaurant">
                            <img src="@/assets/Images/img-placeholder.png" width="60" alt="restaurant-logo" />
                            <p>{{ o.restaurant }}</p>
                        </td>
                        <td>
                            <div class="t-detail">
                                <p>{{ o.quantity }}</p>
                                <a @click="toDetail(o)">{{ o.detail }}</a>
                            </div>
                        </td>
                        <td class="t-hour">
                            {{ o.hour }}
                        </td>
                        <td class="t-hour">
                            {{ o.hourDelivery }}
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                fromDate: null,
                toDate: null,
                menu1: false,
                menu2: false,
                years: [2017, 2018, 2019, 2020, 2021],
                months: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre',
                    'Octubre',
                    'Noviembre', 'Diciembre'
                ],
                orders: [{
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        hourDelivery: "10:25 Pm",
                        nameUser: "Carlos Duarte",
                        restaurant: "Restaurante 0",
                        numOrder: "#128332052",
                        quantity: 2,
                        status: 0,
                    },
                    {
                        detail: "Salchipapa, Gaseo...",
                        hour: "09:16 Pm",
                        nameUser: "Martha Rodriguez",
                        hourDelivery: "10:25 Pm",
                        restaurant: "Restaurante 1",
                        numOrder: "#129332052",
                        quantity: 1,
                        status: 0,
                    },
                ],
                heads: ['', 'Restaurante', 'Pedido', 'Hora de orden', 'Hora de entrega']
            }
        },
         methods: {
            toDetail() {
                this.$router.push("/order");
            },
        },
        created() {
            this.$store.state.title = "Historial";
            for (const item of this.buttons) {
                item.quantity = this.orders.filter((order) => {
                    return order.status == item.key;
                }).length;
            }
        },
    }
</script>

<style scoped>
    .container-fluid {
        padding-left: 122px;
        padding-right: 102px;
        padding-top: 35px;
    }

    .content-time {
        background-color: #E8EDF6;
        border-radius: 10px;
        padding: 15px 15px 15px 24px;

    }

    .txt-time {
        font-size: 1.1rem;
        font-weight: 700;
        color: #959595;
    }

    .content-time>>>.v-label {
        color: #666666;
        font-size: 1.1rem;
    }

    .content-time>>>.v-select__selection {
        color: #666666 !important;
        font-size: 1.1rem;
    }

    .centered-input>>>input {
        text-align: center
    }

    .edt-date {
        max-width: 12rem;
        margin-top: 0;
        padding-top: 0;

    }

    .centered-input>>>.v-text-field__slot input {
        color: #666666;

    }

    .centered-input>>>.v-input__slot {
        background-color: white;
        border-radius: 8px;
        padding-right: 1rem;
        border-style: none !important;
        border-color: none !important;
    }

    .centered-input>>>.v-input__slot::before {
        border-style: none !important;
        border-color: none !important;
    }

    .content-time>>>.v-input__slot {
        background-color: white !important;
        border-radius: 8px !important;
        box-shadow: none !important;
        height: 2.5rem;
        font-size: 1.2rem;
        margin: 0;
    }

    .edt-date>>>.v-input__append-inner {
        margin-top: 8px;
    }

    .content-time>>>.v-text-field__details {
        display: none;
    }

    h1 {
        text-align: left;
        font: normal normal bold 50px/63px Source Sans Pro;
        color: #686868;

    }

    .t-head{
        font: normal normal 400 18px/25px Noto Sans;
        color: #6b6b6b;
    }

    td {
        vertical-align: middle;
    }

    tr {
        border: 1px solid #eaeaea;
        border-radius: 0px 0px 15px 15px;
    }

    .t-none {
        border: none;
    }

    table td,
    table th {
        border-top: none;
    }

    h2 {
        font: normal normal bold 20px/27px Noto Sans;
        color: #626262;
    }

    h3 {
        text-align: left;
        font: normal normal 400 18px/25px Noto Sans;
        color: #6b6b6b;
    }

    p,
    a {
        font: normal normal bold 16px/22px Noto Sans;
        color: #000000;
        margin-bottom: 0;
    }

    a {
        text-decoration: underline;
    }

    .t-detail p {
        width: 28px;
        height: 28px;
        padding-top: 2px;
        margin-right: 12px;
        background: #fffdfd;
        border: 1px solid #d6d6d6;
        box-sizing: border-box;
        border-radius: 50%;
    }

    .t-user,
    .switch,
    .t-detail,
    .buttons {
        display: flex;
        align-items: center;
    }

    .t-user img {
        margin-right: 21px;
        margin-left: 30px;
    }

    .switch {
        justify-content: flex-end;
    }

    .t-detail {
        justify-content: center;
    }
</style>